:root {
  --font-color: #3d4e61;
  --border-color: #e1e7ed;
  --shadow-color: #0000003f;
  --gradient-min-color: #fff;
  --gradient-max-color: #f6f6f7;
}

* {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.search-bar {
  color: var(--font-color);
  justify-content: center;
  width: 100%;
  font-family: Avenir-Book, sans-serif;
  display: flex;
}

.search-bar--large {
  height: 70px;
  font-size: 16px;
}

.search-bar--small {
  height: 42px;
  font-size: 14px;
}

.search-bar__label {
  width: 100%;
  position: relative;
}

.search-bar__label:after {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  position: absolute;
}

.search-bar--large__label:after {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.search-bar--small__label:after {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.search-bar__input {
  font: inherit;
  border: 1px solid var(--border-color);
  width: 100%;
  height: 100%;
}

.search-bar__input:focus {
  background: linear-gradient(to bottom, var(--gradient-min-color), var(--gradient-max-color));
  font-family: Avenir-Heavy, sans-serif;
}

.search-bar--large__input {
  border-radius: 3px;
  padding-left: 62px;
}

.search-bar--small__input {
  border-radius: 4px;
  padding-left: 33px;
}

.search-bar:hover {
  box-shadow: 0 4px 4px var(--shadow-color);
}

.main {
  box-sizing: border-box;
  margin: 8px;
}

.main__search-bar {
  margin: 20px auto 0;
}

@font-face {
  font-family: Avenir-Book;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir-Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}



